.App {
  text-align: center;
  background-color: rgb(124, 185, 175);
  display: flex;
  align-items: center;
  justify-content: center;
}
body
{
  background-color: rgb(124, 185, 175);
}
/* __________________________HEADER_____________________________________ */
header
{
  background-color: rgb(221, 195, 87);
  width: 100%;
  
}
header h1
{
  font-family: 'Lemonada', sans-serif;
  color: #7d3b00;

}
/* Define keyframes for the animation */
@keyframes upDownAnimation {
  0%, 100% {
    transform: translateY(0); /* Initial and final position */
  }
  50% {
    transform: translateY(-20px); /* Midpoint position, adjust the value as needed */
  }
}

/* ______________________________________________ EnterPageComponent _________________________________________ */
.EnterPageComponent
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.EnterPageComponent h2
{
  font-family: 'Kufam', sans-serif;
  font-size: 15px;

}
/* ____________________________StartGetDivEnterPageComponent________________________________ */
.StartGetDivEnterPageComponent
{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  padding-top: 10%;
  padding-bottom: 5%;

}
.StartGetDivEnterPageComponent h2
{
  font-size: 20px;
}
#Start
{
 color: #7d3b00;
 position: relative;
 transform: rotate(340deg);
}
#Get
{
 color: #00677c;
 position: relative;
 transform: rotate(20deg);
}
/*-------------- OpenCloseDivInEnterPageComponent -----------------*/
.OpenCloseDivInEnterPageComponent
{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 90%;
}
.OpenCloseDivInEnterPageComponent img
{
  width: 70%;
}
.OpenCloseDivInEnterPageComponent h2
{
  border: 5px solid rgb(73, 49, 42);
  width: 130px;
  height: 40px;
  padding-top: 3%;
  border-radius: 50px; 
  color: white;
}
/* ---------------------- ParagraphEnterPageComponent ----------------------- */
.ParagraphEnterPageComponent p
{
  font-size: 18px;
  font-family: 'Kufam', sans-serif;
  font-weight: 700;
  padding-top: 10%;
  padding-bottom: 30%;
  margin: 0px;
}
/* -----------------------------ArrowDivEnterPageComponent-------------------------- */
.ArrowDivEnterPageComponent
{
  width: 80%;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 5%;
  animation: upDownAnimation 3s infinite; /* 1s duration, infinite loop */

}
.ArrowEnterPageComponent
{
  width: 40%;
  position: relative;
  transform:scaleX(-1) rotate(359deg);
  
  
}
.ArrowSecondEnterPageComponent
{
  width: 40%;
  position: relative;
  transform: rotate(357deg);
  animation: upDownAnimation 5s infinite; /* 1s duration, infinite loop */

}

/* __________________________________TakeAwayOrInComponent__________________________________ */
.TakeAwayOrInComponent
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.TakeAwayOrInComponent div
{
  margin-top: 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.TakeAwayOrInComponent h2
{
  font-size: 20px;
  font-family: 'Kufam', sans-serif;
  margin-bottom: 20%;
}

.TakeAwayOrInComponent button
{
  width: 250px;
  height: 80px;
  margin-bottom: 10%;
  border-radius: 10px;
  border: 8px solid #7d3b00;
  background-color: rgb(221, 195, 87);
  font-size: 25px;
  font-weight: 700;
  font-family: 'Kufam', sans-serif;
}
/* ____________________________________Menu && MealsComponent_________________________________ */
.Menu
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.SpecialMenuMenuComponent
{
  font-size: 30px;
  text-align: left;
  color:#7d3b00;
  font-weight: bold;
  font-family: 'Kufam', sans-serif;
}
.MealShowMenu
{
  display: grid;
  grid-template-columns: 1fr 1fr ;
  grid-template-rows: 1fr 1fr;
}
.MealsComponent
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 10%;
  margin-top: 10%;

}
.MealsComponent h2
{
  font-family: 'Kufam', sans-serif;
  margin: 0px;
  border: 3px solid rgb(221, 195, 87);
  border-top: 0px;
  width: 80%;
}
.MealImageMealsComponent
{
  width: 80%;
  border: 3px solid rgb(221, 195, 87);
  border-radius: 10px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;

}




















/* ________________________________________FooterComponent________________________________________ */
.FooterComponent
{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  position: relative;
  left: 0px;
  bottom: 0px;
  border-top: 3px solid rgb(221, 195, 87);
}
.FooterComponent a
{
  /* display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; */
  margin: 0px;
  padding: 0px;
  border-left: 3px solid rgb(221, 195, 87);
  padding-top: 2%;
  padding-bottom: 2%;
  border-bottom: 3px solid rgb(221, 195, 87);


}

.FooterComponent img
{
  width: 40%;
  
}


